import React from 'react'
import Video from './Video'
import Participants from './Participants'
import ChatForm from './ChatForm'

export default function ChatLements({remoteSrc, localSrc, participants, dispatch}) {
    return <div id="ChatContainer">
    <div className="VideoContainer">
        {remoteSrc && <Video src={remoteSrc} id={"remoteVideo"} />}
        {localSrc && <Video src={localSrc} id={"localVideo"} />}
    </div>
    {Object.keys(participants).length > 0 && <Participants participants={participants} />}
    <ChatForm dispatch={dispatch} />
</div>
}


