import React, { Component } from 'react'
import Participants from './Participants'
import ChatForm from './ChatForm'

import ChatElements from './ChatElements'
import ErrorMessage from './ErrorMessage'

import MuiThemeProvider from 'material-ui/styles/MuiThemeProvider';

import Video from './Video'

class ChatScreen extends Component {

	componentDidMount() {
		this.props.dispatch({type: 'REQUEST_VIDEO'})
	}

	render() {
		const { localSrc, remoteSrc, participants, dispatch } = this.props
		let chat
		if (!localSrc) {
			chat = <ErrorMessage />
		} else {
			chat = <ChatElements localSrc={localSrc} remoteSrc={remoteSrc} participants={participants} dispatch={dispatch} />;
		}
		return(
			<MuiThemeProvider>
				{chat}
			</MuiThemeProvider>
		)
	}
}

export default ChatScreen

